.calculator {
  background-color: var(--cl-light-bg);

  &__content {
    max-width: var(--content-total-width);
    margin-block: 60px;
    margin-inline: auto;
    padding-inline: var(--content-padding-inline);
  }

  &__title {
    display: flex;
    flex-direction: column;
    gap: 15px;
    font-size: 45px;
    font-weight: 400;
    line-height: 45px;
    text-align: center;
    text-transform: uppercase;
  }

  &__subtitle {
    font-size: 22px;
    font-weight: 300;
  }

  &__text {
    font-size: 14px;
    text-align: center;
  }

  &__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 820px;
    margin-top: 20px;
    margin-inline: auto;
  }

  &__items {
    width: 100%;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding-block: 14px;

    &_new {
      animation-name: slide-in;
      animation-duration: 1s;
      animation-fill-mode: forwards;
    }
  }


  &__property {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__property-name {
    color: #212121;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 0;
    font-family: 'PTSans', sans-serif;
  }

  &__property-value {
    padding: 8px 12px;
    font-size: 14px;
    border: 2px solid var(--cl-accent-1);
    border-radius: 6px;
    box-shadow: 3px 3px 4px 0px rgba(34, 60, 80, 0.2);

    &_select {
      -webkit-appearance: none;
      -moz-appearance: none;
    }
  }

  &__property-option {}

  &__item-summary {
    max-width: 820px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 8px;
    margin-inline: auto;
    padding: 10px 24px;
    border-radius: 2px;
    background-color: var(--cl-accent-1);
    box-shadow: 3px 3px 6px 0px rgba(34, 60, 80, 0.2);

  }

  &__item-text {
    font-size: 15px;
    color: rgba(33, 33, 33, .7);
  }

  &__item-value {
    font-size: 21px;
    color: #212121;
    font-weight: 700;
  }

  &__add-button {
    margin-block: 20px;
    margin-inline: auto;
    padding: 12px 24px;
    font-size: 15px;
    color: var(--cl-grey);
    background-color: var(--cl-light-bg);
    border: 2px solid var(--cl-grey);
    border-radius: 50px;
    font-weight: 600;
    transition: color .3s linear, background-color .3s linear;

    &:hover {
      color: var(--cl-light-bg);
      background-color: var(--cl-grey);
    }

    &:active {
      translate: 0px 2px;
    }
  }

  &__total-summary {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 8px;
    margin-inline: auto;
    margin-inline: 14px;
    padding: 10px 24px;
    border-radius: 2px;
    background-color: var(--cl-accent-1);
    box-shadow: 3px 3px 6px 0px rgba(34, 60, 80, 0.2);
  }

  &__total-text {
    font-size: 15px;
    color: rgba(33, 33, 33, .7);
  }

  &__total-value {
    font-size: 21px;
    color: #212121;
    font-weight: 700;
  }
}

.h2 {}

@keyframes slide-in {
  from {
    translate: -100% 0;
    opacity: 0;
  }

  to {
    translate: 0 0;
    opacity: 1;
  }
}