.scroll-lock {
  overflow: hidden;
}

a.focus-visible {
  outline: auto;
  outline-offset: 2px;
}

button.focus-visible {
  outline: auto;
  outline-offset: 2px;
}

.page {
  height: 100%;
  font-family: var(--font-family, system-ui, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
  scroll-behavior: smooth;
}

.body {
  background-color: var(--cl-light-bg);
}

.container {
  min-width: 320px;
  min-height: 100vh;
  min-height: 100dvh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

.h1 {}

.h2 {}

.visualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}