:root {
  --font-family: 'OpenSans', sans-serif;
  --cl-black: hsl(0, 0%, 0%);
  --cl-grey: hsl(0, 0%, 58%);
  --cl-text: var(--cl-black);
  --cl-light-bg: hsl(0, 0%, 100%);
  --cl-dark-bg: hsl(240, 1%, 14%);
  --cl-accent-1: hsl(49, 100%, 50%);
  --cl-accent-2: hsl(205, 20%, 17%);
  --cl-accent-3: hsl(358, 86%, 33%);
  --linear-gradient-1: linear-gradient(0deg, #f29412 0, #ffd012 100%);
  --cl-header-text: var(--cl-text);
  --cl-header-bg: var(--cl-accent-1);
  --cl-input-br: var(--cl-accent-1);
  --content-width: 1300px;
  --content-padding-inline: 36px;
  --content-total-width: calc(var(--content-width) + var(--content-padding-inline)*2);
}