@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Light.woff2"), url("../fonts/OpenSans-Light.woff"), url("../fonts/OpenSans-Light.ttf");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2"), url("../fonts/OpenSans-Regular.woff"), url("../fonts/OpenSans-Regular.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-SemiBold.woff2"), url("../fonts/OpenSans-SemiBold.woff"), url("../fonts/OpenSans-SemiBold.ttf");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "PTSans";
  src: url("../fonts/PTSans-Regular.woff2"), url("../fonts/PTSans-Regular.woff"), url("../fonts/PTSans-Regular.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Trebuchetms";
  src: url("../fonts/Trebuchetms-Regular.woff2"), url("../fonts/Trebuchetms-Regular.woff"), url("../fonts/Trebuchetms-Regular.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
:root {
  --font-family: "OpenSans", sans-serif;
  --cl-black: hsl(0, 0%, 0%);
  --cl-grey: hsl(0, 0%, 58%);
  --cl-text: var(--cl-black);
  --cl-light-bg: hsl(0, 0%, 100%);
  --cl-dark-bg: hsl(240, 1%, 14%);
  --cl-accent-1: hsl(49, 100%, 50%);
  --cl-accent-2: hsl(205, 20%, 17%);
  --cl-accent-3: hsl(358, 86%, 33%);
  --linear-gradient-1: linear-gradient(0deg, #f29412 0, #ffd012 100%);
  --cl-header-text: var(--cl-text);
  --cl-header-bg: var(--cl-accent-1);
  --cl-input-br: var(--cl-accent-1);
  --content-width: 1300px;
  --content-padding-inline: 36px;
  --content-total-width: calc(var(--content-width) + var(--content-padding-inline)*2);
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a:focus,
a:active {
  outline: none;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img,
svg {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.scroll-lock {
  overflow: hidden;
}

a.focus-visible {
  outline: auto;
  outline-offset: 2px;
}

button.focus-visible {
  outline: auto;
  outline-offset: 2px;
}

.page {
  height: 100%;
  font-family: var(--font-family, system-ui, sans-serif);
  -webkit-text-size-adjust: 100%;
  font-size: 16px;
  scroll-behavior: smooth;
}

.body {
  background-color: var(--cl-light-bg);
}

.container {
  min-width: 320px;
  min-height: 100vh;
  min-height: 100dvh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

.visualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: var(--cl-header-bg);
}
.header__content {
  max-width: var(--content-total-width);
  margin-top: 20px;
  margin-bottom: 10px;
  margin-inline: auto;
  padding-inline: var(--content-padding-inline);
}
.header__top {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 16px;
  font-family: var(--font-family);
  font-size: 36px;
  font-weight: 600;
  font-style: italic;
}
.header__logo-img {
  max-height: 96px;
}
.header__schedule {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
}
.header__schedule-item {
  font-weight: 600;
}
.header__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 10px;
}
.header__contact {
  padding: 2px;
  font-family: "Trebuchetms";
  font-size: 16px;
  font-weight: 600;
  color: var(--cl-text);
}
.header__contact:focus-visible {
  outline: 2px outset currentColor;
}
.header__list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 40px;
}
